@import './variables';

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url('../images/starry-sky.jpg');
  padding: 20px;
  min-height: 100vh;
  box-sizing: border-box;
}

.header {
  width: 100%;
  margin-bottom: 20px;
}

.menu-link {
  color: $color-white;
  text-decoration: none;
  font-size: $font-sm;
  @media all and (min-width: 768px) {
    font-size: $font-med;
  }
  @media all and (min-width: 1440px) {
    font-size: $font-lg;
  }
}

.main-content {
  width: 100%;
}

.menu-list {
  padding: 0;
  margin: 0;
}